// main page elements
//	font-family: 'Montserrat'
//	font-style: normal
//	font-weight: normal
body {
	font-family: 'Montserrat';
	font-style: normal;
	font-size: 14px;
	font-weight: 400;
	background: white;
	overflow-x: hidden;
	background: #E5E5E5;
	color: $black; }
.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 1570px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }
.heshteg_grey {
	font-weight: 800;
	font-size: 21px;
	line-height: 26px;
	text-transform: uppercase;
	color: $gray_sub_2; }
.heshteg_black {
	font-weight: bold;
	font-size: 21px;
	line-height: 26px;
	text-transform: uppercase;
	color: $black; }
.heshteg_dop {
	font-weight: bold;
	font-size: 20px;
	line-height: 24px;
	text-transform: uppercase;
	color: $white_sub_2; }
.p_dop {
	font-weight: bold;
	font-size: 22px;
	line-height: 27px;
	color: white; }
.p_main {
	font-weight: normal;
	font-size: 16px;
	line-height: 32px;
	color: $black; }
.btn_main {
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 274px;
	height: 71px;
	background: linear-gradient(87.43deg, #F44900 0%, #E5740D 54.18%, #FA7E0C 98.58%);
	border-radius: 53px;
	font-weight: bold;
	font-size: 16px;
	line-height: 20px;
	color: #FFFFFF; }
